import React from "react";
import Prism from "prismjs";
import copy from "copy-to-clipboard";
import { Icon } from './Icon';

Prism.languages.http = {
  tag: {
    pattern: /{(.*?)}/i,
  },
};


export type CodeBlockProps = {
  name?: string
  language: string
  children?: React.ReactNode
}

export function CodeBlock(props: CodeBlockProps) {
  const [copied, setCopied] = React.useState(false);
  const ref = React.useRef<HTMLPreElement>(null);

  React.useEffect(() => {
    if (ref.current) Prism.highlightElement(ref.current, false);
  }, [props.children]);

  React.useEffect(() => {
    if (ref.current && copied) {
      copy(ref.current.innerText);
      const to = setTimeout(setCopied, 1000, false);
      return () => clearTimeout(to);
    }
  }, [copied]);

  const lang = props.language !== "" ? props.language : "qqq";

  const lines =
    typeof props.children === 'string' ? props.children.split('\n').filter(Boolean) : [];

  return (
    <div className="code" aria-live="polite">
      <pre
        // Prevents "Failed to execute 'removeChild' on 'Node'" error
        // https://stackoverflow.com/questions/54880669/react-domexception-failed-to-execute-removechild-on-node-the-node-to-be-re
        // key={props.children}
        ref={ref}
        className={`language-${lang}`}
      >
        {props.children}
      </pre>
      {<button onClick={() => setCopied(true)}>
        <Icon icon={copied ? 'copied' : 'copy'} />
      </button>}
      <style jsx>
        {`
          .code {
            position: relative;
          }
          .code button {
            appearance: none;
            position: absolute;
            color: inherit;
            background: var(--code-background);
            top: ${lines.length === 1 ? '17px' : '13px'};
            right: 11px;
            border-radius: 4px;
            border: none;
            font-size: 15px;
          }
        `}
      </style>
    </div>
  );
}
